<template>
  <v-flex xs12>
    <svg :width="width" :height="height" class="grey darken-3">
      <g :transform="`translate(${marginLeft}, ${marginTop})`">
        <!-- linea a predecir (blue) -->
        <c-path
          :rangeX="[0, total.length - 1]"
          :dataset="total"
          column="y"
          :height="chartHeight"
          :width="chartWidth"></c-path>
        <!-- linea de prediccion (yellow) -->
        <c-path
          v-if="total.length > 0"
          :transform="`translate(${$toXPixel(toPredict.length - 1, 0, total.length - 1, chartWidth)}, 0)`"
          color="yellow"
          :rangeX="[0, total.length - 1]"
          :rangeY="[
            $getMin(total, 'y'),
            $getMax(total, 'y')
          ]"
          :dataset="prediction"
          column="y"
          :height="chartHeight"
          :width="chartWidth"></c-path>
        <!-- marcas de anomalias -->
        <line 
        v-for="(line, i) in anomalies"
        :key="i"
        :x1="line" y1="0" :x2="line" :y2="chartHeight"
        stroke-linejoin="round"
        stroke-linecap="round"
        stroke-width="1.5"
        stroke="red"/>
        <!-- ejes -->
        <x-axis :range="[0, total.length - 1]" :ticks="10" :fixed="1" :height="chartHeight" :width="chartWidth"></x-axis>
        <y-axis :range="[$getMin(total, 'y'), $getMax(total, 'y')]" :ticks="5" :fixed="1" :height="chartHeight" label="Units"></y-axis>
      </g>
    </svg>
  </v-flex>
</template>

<script>
export default {
  props: {
    dataSet: null,
    height: {
      type: Number,
      default: 500
    },
    marginLeft: {
      type: Number,
      default: 40
    },
    marginTop: {
      type: Number,
      default: 40
    },
    marginRight: {
      type: Number,
      default: 20
    },
    marginBottom: {
      type: Number,
      default: 30
    },
    padding: {
      type: Number,
      default: 12
    }
  },
  data: () => ({
    width: null,
    chartWidth: 1000,
    chartHeight: 400,
    total: [],
    toPredict: [],
    prediction: [],
    anomalies: []
  }),
  mounted () {
    this.width = this.$el.clientWidth
    this.chartWidth = this.width - (this.marginLeft + this.marginRight)
    this.chartHeight = this.height - (this.marginTop + this.marginBottom)
  },
  methods: {
    processData (d) {
      for (let i = 0; i < d.toPredict.length; i++) {
        this.toPredict.push({
          x: i,
          y: d.toPredict[i]
        })
        this.total.push({
          x: i,
          y: d.toPredict[i]
        })
      }
      this.toPredict.push({
        x: d.toPredict.length,
        y: d.prediction.future[0].value
      })
      for (let i = 0; i < d.prediction.future.length; i++) {
        this.prediction.push({
          x: d.prediction.future[i].step,
          y: d.prediction.future[i].value
        })
        this.total.push({
          x: d.prediction.future[i].step,
          y: d.prediction.future[i].value
        })
      }
      for (let i = 0; i < d.prediction.past.length; i++) {
        let xMark = this.$toXPixel(d.prediction.past[i].step, 0, this.total.length - 1, this.chartWidth)
        this.anomalies.push(xMark)
      }
      // let xMark = this.$toXPixel(134, 0, this.total.length - 1, this.chartWidth)
      // this.anomalies.push(xMark)
    },
    reset () {
      this.total = []
      this.toPredict = []
      this.prediction = []
      this.anomalies = []
    }
  },
  watch: {
    dataSet: {
      handler: function (val) {
        if (val.toPredict && val.prediction) {
          this.$nextTick(() => {
            this.reset()
            this.processData(val)
          })
        }
      },
      immediate: true
    }
  }
}
</script>

<style>
  svg {
    user-select: none;
  }
</style>



