<template>
  <g
  text-anchor="middle"
  fill="white"
  stroke="white"
  stroke-width="1"
  :transform="`translate(0, ${height})`">
    <line :x2="width"></line>
    <g v-for="(tick, i) in ticksList" :key="'tick' + i"
    :transform="`translate(${i * (width/(ticksList.length - 1))}, 0)`">
      <line y2="6"></line>
      <text stroke-width="0.1" y="9" dy="0.71em">{{tick}}</text>
    </g>
  </g>
</template>

<script>
export default {
  name: 'x-axis',
  props: {
    range: {
      type: Array,
      required: true
    },
    ticks: {
      type: Number
    },
    height: {
      type: Number,
      required: true
    },
    width: {
      type: Number,
      required: true
    },
    fixed: {
      type: Number
    }
  },
  computed: {
    ticksList () {
      if (this.range && this.range.length > 0) {
        let list = []
        const min = this.range[0]
        const max = this.range[1]
        const d = (max - min) / (this.ticks - 1)
        let o = 0
        list.push(0)
        for (let i = 1; i < this.ticks; i++) {
          list.push(this.fixed ? (o += d).toFixed(this.fixed) : (o += d))
        }
        return list
      }
    }
  }
}
</script>



